.DataAnalysis{
  display: flex;
  flex-direction: column;
  &-search{
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
  }
  &-formItem{
    margin-right: 15px;
    width: 260px;
  }
  &-content{
    display: flex;
    flex: 1;
    &Left{
      display: flex;
      flex-direction: column;
      width: 260px;
      max-height: 70vh;
      background: #FFFFFF;
      box-shadow: 0px 0px 40px 0px rgba(0, 0, 0, 0.08);
      border-radius: 2px;
      overflow: auto;
      &Title{
        font-size: 16px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: #333333;
        line-height: 22px;
        padding-left: 40px;
        margin-top: 30px;
      }
    }
    &Middle{
      display: flex;
      width: 500px;
      max-height: 700px;
      flex-direction: column;
      margin-left: 10px;
      &Header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        &Left{
          width: 353px;
          margin-left: 20px;
          display: flex;
          &Title{
            font-size: 16px;
            font-family: PingFangSC-Semibold, PingFang SC;
            font-weight: 600;
            color: #333333;
          }
        }
        &Right{
          cursor: pointer;
          color: #096DD9;
          &.disabled{
            cursor: not-allowed;
            color: rgba(9,109,217,0.5);
          }
        }
      }
    }
    &Right{
      display: flex;
      flex: 1;
      padding-left: 30px;
      flex-direction: column;
    }
  }
  &-pie{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &-standard{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &-line{
    width: 100%;
    margin-top: 10px;
    position: relative;
  }
  &-previous{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 42px;
    height: 42px;
    background: blue;
  }
  &-next{
    position: absolute;
    right: 0;
    bottom: 0;
    width: 42px;
    height: 42px;
    background: blue;
  }
  .ant-tree-title{
    color: #222;
  }
}

@primary-color: #1890ff;