.ControlTableList{
  &-clear{
    font-size: 16px;
    font-family: SourceHanSansSC-Normal, SourceHanSansSC;
    font-weight: 400;
    color: #096DD9;
    cursor: pointer;
    line-height: 14px;
  }
  &-header{
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &Left{
      display: flex;
      align-items: center;
      height: 100%;
    }
    &Form{
      display: flex;
      height: 100%;
      align-items: center;
      &Item{
        margin-left: 15px;
        font-size: 16px;
        display: flex;
        font-family: SourceHanSansSC-Normal, SourceHanSansSC;
        font-weight: 400;
        color: #222222;
        line-height: 14px;
      }
      &Controls{
        width: 130px;
      }
    }
    &Right{
      display: flex;
      height: 100%;
      align-items: center;
      justify-content: flex-end;
    }
  }
  &-advancedSearch{
    margin-left: 15px;
    font-size: 16px;
    font-family: SourceHanSansSC-Normal, SourceHanSansSC;
    font-weight: 400;
    color: #096DD9;
    line-height: 14px;
    cursor: pointer;
  }
  .ant-form-item{
    margin-bottom: 0px;
  }
  .ant-form-item-label > label{
    font-size: 16px;
    font-family: SourceHanSansSC-Normal, SourceHanSansSC;
    font-weight: 400;
    color: #222222;
    line-height: 14px;
  }
  .ant-form-item-label > label::after{
    content: "";
  }
}

@primary-color: #1890ff;